.icon-container {
    cursor:pointer;
    display:flex;
    justify-content:center;
    align-items:center;
    height:48px;
    width:48px;
    font-size:24px;
    font-weight:700;
}

.icon-container-check {
    position:absolute;
    left:12px;
}

.icon-container-check:hover .icon-svg {
    fill: green;
}

.icon-container-delete {
    position:absolute;
    top:-24px;
    right:0
}

.icon-container-delete:hover .icon-svg {
    fill: red;
}

.icon-svg {
    height: 25px;
    width: 25px;
}